import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';

import Forschool from './panels/Forschool';
import Forsummer from './panels/Forsummer';
import Forhome from './panels/Forhome';
import Forthecar from './panels/Forthecar';

import Offer from './panels/Forschool';
import Oservise from './panels/Oservise';

import Sch1 from './panels/Sch1';
import Sch2 from './panels/Sch2';

import Sum1 from './panels/Sum1';
import Sum2 from './panels/Sum2';

const App = () => {
 const [activePanel, setActivePanel] = useState('home');
 const [fetchedUser, setUser] = useState(null);
 const [popout, setPopout] = useState(null);
  

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};


	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} />

			<Forschool id='forschool' go={go} />
			<Forsummer id='forsummer' go={go} />
			<Forhome id='forhome' go={go} />
			<Forthecar id='forthecar' go={go} />

			<Offer id='offer' go={go}/>
			<Oservise id='oservise' go={go} />

			<Sch1 id='sch1' go={go} />
			<Sch2 id='sch2' go={go} />

			<Sum1 id='sum1' go={go} />
			<Sum2 id='sum2' go={go} />
		</View>
	);
}

export default App;

